import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validationRosok from "../../Validations/validationRosok";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { SkeletonLoading } from "../../GlobalComponent/function";

const maptoState = (state) => {
  return {
    initialValues: {
      kodeJenis: state.provinsi.editRosok.kodeJenis,
      statusAktif: state.provinsi.editRosok.statusAktif,
      kodeKategori: state.provinsi.editRosok.kodeKategori,
      namaJenis: state.provinsi.editRosok.namaJenis,
      kadarBeli: state.provinsi.editRosok.kadarBeli,
      kadarJual: state.provinsi.editRosok.kadarJual,
      kadarCetak: state.provinsi.editRosok.kadarCetak,
    },
  };
};

class FormKodeRosokEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      listPerhiasan: [],
      listJual: [],
      listBeli: [],
      listKategori: [],
    };
  }

  submit = () => {
    console.log(JSON.stringify(this.state));
  };

  componentDidMount() {
    AxiosMasterGet("kadar/get/by-type/kadar-cetak")
      .then((res) => {
        this.setState({
          listPerhiasan: res.data,
        });
      })
      .catch((err) => console.log(err));
    AxiosMasterGet("kadar/get/by-type/kadar-beli")
      .then((res) => {
        this.setState({
          listBeli: res.data,
        });
      })
      .catch((err) => console.log(err));
    AxiosMasterGet("kadar/get/by-type/kadar-jual")
      .then((res) => {
        this.setState({
          listJual: res.data,
        });
      })
      .catch((err) => console.log(err));
    AxiosMasterGet("kategori/get/all-by-kelompok/CIOK")
      .then((res) => {
        let hasil = res.data;
        AxiosMasterGet("kategori/get/all-by-kelompok/RONGSOK").then((res) => {
          hasil.push(...res.data);
          this.setState({
            listKategori: hasil,
          });
        });
      })
      .catch((err) => console.log(err));
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          {/* Awal FIeld */}
          <div className="col-md-3">
            <div className="form-group">
              {String(this.state.listKategori) !== "" ? (
                <Field
                  component={renderFieldSelect}
                  label="Kode Jenis"
                  name="kodeKategori"
                  value={this.props.status}
                  normalize={(value) => value && value.toUpperCase()}
                  className="form-control"
                  options={this.state.listKategori.map((list) => {
                    let data = {
                      value: `${list.kode_kategori}||${list.kode_kelompok}`,
                      name: list.nama_kategori,
                    };
                    return data;
                  })}
                ></Field>
              ) : (
                <SkeletonLoading label="Kode Jenis" />
              )}
            </div>
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-3">
            <Field
              name="kodeJenis"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Barang"
            />
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-6">
            <Field
              name="namaJenis"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Nama Barang"
            />
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-3">
            <div className="form-group">
              {String(this.state.listPerhiasan) !== "" ? (
                <Field
                  component={renderFieldSelect}
                  label="Cap Kode"
                  name="kadarCetak"
                  value="DEFAULT"
                  className="form-control"
                  options={this.state.listPerhiasan.map((obj) => {
                    var payload = {
                      value: obj.kadar.toString(),
                      name: obj.kadar,
                    };
                    return payload;
                  })}
                ></Field>
              ) : (
                <SkeletonLoading title="Cap Kode" />
              )}
            </div>
          </div>
          {/* AKhir FIeld */}
          {/* Awal Field */}
          <div className="col-md-3">
            <div className="form-group">
              {String(this.state.listBeli) !== "" ? (
                <Field
                  component={renderFieldSelect}
                  label="Kode Awal"
                  name="kadarBeli"
                  value="DEFAULT"
                  className="form-control"
                  options={this.state.listBeli.map((obj) => {
                    var payload = {
                      value: obj.kadar,
                      name: obj.kadar,
                    };
                    return payload;
                  })}
                ></Field>
              ) : (
                <SkeletonLoading title="Kode Awal" />
              )}
            </div>
          </div>
          {/* AKhir FIeld */}
          {/* Awal Field */}
          <div className="col-md-3">
            <div className="form-group">
              {String(this.state.listJual) !== "" ? (
                <Field
                  component={renderFieldSelect}
                  label="Kode Tukar"
                  name="kadarJual"
                  value="DEFAULT"
                  className="form-control"
                  options={this.state.listJual.map((obj) => {
                    var payload = {
                      value: obj.kadar,
                      name: obj.kadar,
                    };
                    return payload;
                  })}
                ></Field>
              ) : (
                <SkeletonLoading title="Kode Tukar" />
              )}
            </div>
          </div>
          {/* AKhir FIeld */}

          <div className="col-lg-12 col-md-12 mt-3">
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info"
                disabled={this.props.onSend}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Submit
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKodeRosokEdit = reduxForm({
  form: "RosokEdit",
  validate: validationRosok,
  enableReinitialize: true,
})(FormKodeRosokEdit);
export default connect(maptoState, null)(FormKodeRosokEdit);
